/* unfinshed project */

* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
}

:root {
	--primary: #f4f4f4;
	--secondary: #333;
	--blue: #5566BB;
	--green: #55BB66;
	--red: #BB5566;
	--light-green: #55DD66;
	--shadow: 0px 1px 2px rgba(0,0,0,0.3);
	--dark-shadow: 0px 5px 25px rgba(0,0,0,0.6);
}

.center-items {
	display: -webkit-flex;
	display: flex;	
	-webkit-align-items: center;	
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
}

body {
	background: #333;
	background: var(--secondary);
	color: #f4f4f4;
	color: var(--primary);
	font-family: 'Montserrat Light';
	width: 100vw;
	height: 100vh;
	overflow: hidden;
}

.container {
	width: 90%;
	margin: auto;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}

.game-title {
	margin: 20px auto 5px auto;
	position: relative;
	overflow: hidden;
	text-align: center;
	font-size: 3rem;
	font-family: 'Rubik';
	font-weight: bold;
	color: #f4f4f4;
}

.menu-card-container {
	width: 310px;
	height: 390px;
	margin: auto;
}

.menu-card {
	width: 300px;
	height: 200px;
	margin: auto;
	padding: 10px;
	position: relative;
	border-radius: 7px;
}

/* Game Menu */
.menu-card-one {
	-webkit-transform: translateY(100px);
	        transform: translateY(100px);
	-webkit-transition: all 0.6s;
	transition: all 0.6s;
	z-index: 1;
	-webkit-animation: background-transition 7s alternate infinite ease-in-out;
	        animation: background-transition 7s alternate infinite ease-in-out;
	overflow: hidden;
}
@-webkit-keyframes background-transition {
	0% {
		background: #55DD66;
		background: var(--light-green);
	}
	50% {
		background: #5566BB;
		background: var(--blue);
	}
	100% {
		background: #BB5566;
		background: var(--red);
	}
}
@keyframes background-transition {
	0% {
		background: #55DD66;
		background: var(--light-green);
	}
	50% {
		background: #5566BB;
		background: var(--blue);
	}
	100% {
		background: #BB5566;
		background: var(--red);
	}
}

.hide--menu {
	border-bottom-left-radius: 0;
	border-bottom-right-radius: 0; 
	-webkit-transform: translateY(0); 
	        transform: translateY(0);
	height: 80px;
}

.rightTopBtn {
	position: absolute;
	right: 10px;
	top: 10px;
	width: 40px;
	height: 40px;
	border-radius: 50%;
	font-size: 2rem;
	line-height: 40px;
	text-align: center;
	cursor: pointer;
}
.rightTopBtn:hover {
	background: rgba(0,0,0,0.3);
}
.fs {
	font-size: 3rem;
}

.menu-center {
	position: relative;
	width: 100px;
	height: 100px;
}
.menu-center:after {
	content: '';
	position: absolute;
	top: -10%;
	left: -10%;
	width: 110%;
	height: 110%;
	border-radius: 50%;
	z-index: -1;
	border: 5px dotted  rgba(0,0,0,0.2);
	box-shadow: 0px 1px 2px rgba(0,0,0,0.3);
	box-shadow: var(--shadow);
	background-clip: padding-box;
	-webkit-animation: rotate 5s alternate infinite;
	        animation: rotate 5s alternate infinite;
}
@-webkit-keyframes rotate {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}
@keyframes rotate {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

.menu-center-image {
	width: 100%;
	height: 100%;
	border-radius: 50%;
	box-shadow: 0px 1px 2px rgba(0,0,0,0.3);
	box-shadow: var(--shadow);
	-webkit-transition: all 0.6s;
	transition: all 0.6s;
	cursor: pointer;
}

.menu-center:hover .menu-center-image {
	box-shadow: 0px 5px 25px rgba(0,0,0,0.6);
	box-shadow: var(--dark-shadow);
}

.hide--menu .menu-center {
	width: 70px;
	height: 70px;
}
.hide--menu .menu-center:after {
	top: -12%;
	left: -12%;
}

.menu {
	position: absolute;
	top: 0;
	left: 0;
	border-radius: 7px;
	width: 100%;
	height: 100%;
	background: #5566BB;
	background: var(--blue);
	-webkit-transition: 0.6s;
	transition: 0.6s;
	z-index: 10;
}
.menu-item {
	-webkit-transform: translateY(0px) !important;
	        transform: translateY(0px) !important;
}
.menu ul {
	margin: 0;
	list-style: none;
}
.menu ul li {
	width: 120px;
	height: 30px;
	margin: 5px auto;
	text-align: center;
	background: rgba(0,0,0,0.4);
	color: #f4f4f4;
	color: var(--primary);
	line-height: 30px;
	letter-spacing: 1px;
	border-radius: 20px;
	text-transform: uppercase;
	cursor: pointer;
}
.menu ul li:hover {
	background: rgba(0,0,0,0.7);
}

/* Game   -Card */
.menu-card-two {
	background: #f4f4f4;
	background: var(--primary);
	box-shadow: 0px 5px 25px rgba(0,0,0,0.6);
	box-shadow: var(--dark-shadow);
	-webkit-transform: translateY(-100px);
	        transform: translateY(-100px);
	-webkit-transition: all 0.7s;
	transition: all 0.7s;
	overflow: hidden;
}

.show-game {
	border-top-left-radius: 0;
	border-top-right-radius: 0; 
	-webkit-transform: translateY(0); 
	        transform: translateY(0);
	padding: 0px 5px;
	height: 300px;
}

.game-card-content {
	color: #333;
	color: var(--secondary);
}

.game-timer-container {
	text-align: center;
	height: 40px;
	font-size: 1em;
	font-family: Rubik;
}

.game-board-container {
	width: 250px;
	height: 250px;
	margin: 5px auto;
	padding: 5px;
}

.board-card-container {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
	width: 100%;
	height: 100%;
	position: relative;
}
.board-card {
	width: 30%;
	height: 30%;
	border-radius: 5px;
	background: #ddd;
	color: #ddd;
	font-size: 4em;
	cursor: pointer;
}
.x-mark {
	background: rgba(170, 172, 50, 0.479);
}
.o-mark {
	background: rgba(73, 128, 201, 0.479)
}
.show {
	color: #fff;
}

.winner-board {
	position: absolute;
	top: -50px;
	left: -30px;
	width: 300px;
	height: 300px;
	background: rgba(0,0,0,0.6);
	color: #fff;
	z-index: 10;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
}
.winner-board-ontainer {
	font-size: 2em;
}
.restart-btn {
	border: none;
	width: 60%;
	height: 45px;
	line-height: 45px;
	background: #5566BB;
	background: var(--blue);
	color: #f4f4f4;
	color: var(--primary);
	font-family: Montserrat Light;
	font-size: 1.2em;
	box-shadow: 0px 1px 2px rgba(0,0,0,0.3);
	box-shadow: var(--shadow);
	border-radius: 5px;
}
.restart-btn:hover{
	box-shadow: 0px 5px 25px rgba(0,0,0,0.6);
	box-shadow: var(--dark-shadow);
}

.menuContainer {
	width: 80%;
	height: 80%;
	margin: 10% auto;
	font-family: Rubik;
}
.playMenu p {
	text-align: center;
	padding: 10px;
	font-size: 1.4rem;
	font-family: Rubik;
	width: 100%;
	height: 25%;
}
.playMenuChoice {
	height: 75%;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: space-around;
	        justify-content: space-around;
}
.playMenu span {
	width: 70px;
	height: 70px;
	font-size: 5em; 
	overflow: hidden;
	line-height: 72px;
	text-align: center;
	margin: auto;
	border-radius: 50%;
	transitioN: all 0.6s;
}
.playMenu span:first-child {
	-webkit-transform: rotate(180deg) translateY(30px);
	        transform: rotate(180deg) translateY(30px);
}
.playMenu span:last-child {
	-webkit-transform: rotate(-180deg) translateY(30px);
	        transform: rotate(-180deg) translateY(30px);
}
.playMenu span:first-child {
	line-height: 60px;
}

.playMenu span:hover {
	background:rgba(0,0,0,0.3);
	box-shadow: inset 0px 1px 2px rgba(0,0,0,0.3);
	box-shadow: inset var(--shadow);
	-webkit-transform: rotate(0deg) translateY(0px) scale(1.2);
	        transform: rotate(0deg) translateY(0px) scale(1.2);
}
.playMenu span:active {
	background:rgba(0,0,0,0.5);
	box-shadow: 0px 5px 25px rgba(0,0,0,0.6);
	box-shadow: var(--dark-shadow);
}

/* about */
.about_title {
	font-size: 12px;
	display: block;
	margin: 5px 0px;
}
.about_title1 {
	margin: 15px 0px;
	width: 100%;
	border-bottom: 1px solid #ddd;
}
.about_description {
	margin: 10px 0 0 0;
	font-size: 11px;
}
.about_date {
	font-size: 13px;
	width: 100px;
	float: right;
}


/* Large Screen Media */
@media screen and (min-width: 650px) {
	body {
		font-size: 16px;
	}

	.game-title {
		font-size: 4rem;
		text-shadow: inset 0px 5px 25px rgba(0,0,0,0.6);
		text-shadow: inset var(--dark-shadow);
	}

	.menu-card-container {
		margin: 2em auto;
		width: 410px;
		height: 520px;
	}

	.menu-card {
		width: 400px;
		height: 300px;
		padding: 1em;
		border-radius: 10px;
	}

	.menu-card-one {
		-webkit-transform: translateY(100px);
		        transform: translateY(100px);
		-webkit-transition: all 0.2s;
		transition: all 0.2s;
	}

	.menu-card-two {
		-webkit-transform: translateY(-200px);
		        transform: translateY(-200px);
		-webkit-transition: all 0.8s;
		transition: all 0.8s;
	}

	.hide--menu {
		border-bottom-left-radius: 0;
		border-bottom-right-radius: 0; 
		-webkit-transform: translateY(0); 
		        transform: translateY(0);
		height: 100px;
	}
	.hide--menu .menu-center {
		width: 90px;
		height: 90px;
	}
		
	.hide--menu .menu-center:after {
		top: -10%;
		left: -10%;
	}

	.menu-center {
		width: 160px;
		height: 160px;
	}

	.menu-center:after {
		top: -8%;
		left: -8%;
	}

	.show-game {
		border-top-left-radius: 0;
		border-top-right-radius: 0; 
		-webkit-transform: translateY(0); 
		        transform: translateY(0);
		height: 400px;
	}

	.game-timer-container {
		height: 40px;
		font-size: 1.5em;
	}
	.game-board-container {
		width: 300px;
		height: 300px;
		margin: 2em auto 0 auto;
		padding: 10px;
	}

	.board-card-container {
		display: -webkit-flex;
		display: flex;
		-webkit-flex-wrap: wrap;
		        flex-wrap: wrap;
		-webkit-justify-content: space-between;
		        justify-content: space-between;
		width: 100%;
		height: 100%;
	}
	.board-card {
		font-size: 5em;
	}

	/* play menu */
	.playMenu p {
		font-size: 2rem;
	}
	.playMenu span {
		width: 100px;
		height: 100px;
		font-size: 7em; 
		line-height: 103px;
		-webkit-transform: rotate(180deg) translateY(45px);
		        transform: rotate(180deg) translateY(45px);
		cursor: pointer;
	}
	.playMenu span:first-child {
		line-height: 85px;
	}

	/* about */
	.about_title {
		font-size: 18px;
		display: block;
		margin: 5px 0px;
	}
	.about_title1 {
		margin: 1.3em 0px;
		padding: 5px;
		font-size: 1.4em;
	}
	.about_description {
		margin: 1em 0 10px 0;
		font-size: 1em;
	}
	.about_date {
		font-size: 16px;
		width: 100px;
		float: right;
		margin-top: -5px;
	}

	.winner-board {
		top: -100px;
		left: -60px;
		width: 400px;
		height: 405px;
	}
	.winner-board-ontainer {
		font-size: 3em;
		margin-top: 10px;
	}
	.restart-btn {
		border: none;
		width: 60%;
		height: 45px;
		line-height: 45px;
		background: #5566BB;
		background: var(--blue);
		color: #f4f4f4;
		color: var(--primary);
		font-family: Montserrat Light;
		font-size: 1.2em;
		box-shadow: 0px 1px 2px rgba(0,0,0,0.3);
		box-shadow: var(--shadow);
		border-radius: 5px;
	}
	.restart-btn:hover{
		box-shadow: 0px 5px 25px rgba(0,0,0,0.6);
		box-shadow: var(--dark-shadow);
	}
}
